<template>
	<transition name="fade">
		<div v-if="slider !== undefined && slider.loaded && display" class="controls">
			<div class="previous" @click="slider.showImage('previous')"></div>
			<div :class="autoplayClass" @click="slider.toggleAutoplay()"></div>
			<div class="next" @click="slider.showImage('next')"></div>
		</div>
	</transition>
</template>

<script>
	export default {
		props: {
			slider: { type: Object, required: false }
		},

		computed: {
			controls: function() {
				return this;
			},

			display: function() {
				if (this.slider.mouseOver !== true)
					return false;

				if (this.slider.transition.current !== undefined)
					return false;

				if (this.slider.index !== undefined && this.slider.index.visible)
					return false;

				return true;
			},

			autoplayClass: function() {
				return this.slider.config.autoplay? 'pause' : 'play';
			}
		}
	};
</script>

<style lang="scss" scoped>
	.fade-enter, .fade-leave-to {
		opacity: 0;
	}

	.fade-enter-active, .fade-leave-active {
		transition: opacity 0.3s ease-in;
	}

	.controls {
		position: absolute;
		top: 50%;
		left: 0;
		right: 0;
		height: 50px;
		margin-top: -25px;
		text-align: center;
		z-index: 100;
	}

	.previous,
	.play,
	.pause,
	.next {
		position: absolute;
		top: 0;
		width: 50px;
		height: 50px;
		cursor: pointer;
		border-radius: 50%;
		background-color: rgba(0, 0, 0, 0.6);
		background-repeat: no-repeat;
		background-position: center center;
		background-size: 40%;
		transition: background-color 0.2s ease-in;
	}

	.previous {
		left: 25px;
		background-image: url(data:image/png;base64,);
	}

	.play {
		position: relative;
		display: inline-block;
		background-image: url(data:image/png;base64,);
	}

	.pause {
		position: relative;
		display: inline-block;
		background-image: url(data:image/png;base64,);
	}

	.next {
		right: 25px;
		background-image: url(data:image/png;base64,);
	}

	.previous:hover,
	.play:hover,
	.pause:hover,
	.next:hover {
		background-color: rgba(0, 0, 0, 0.9);
	}
</style>
